body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #9669F0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.CryptoContainer{
  margin-top: 50px;
  margin-bottom: 50px;
  width: 100vw;
  display: flex;
  flex-direction: row;

}

#header{
  background-color: #211637;
  min-height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

#header div{
  margin: auto;
  color: #B18AFF;
}

#nextDay{
  width: 100vw;
  height: 50px;
  background-color: #B18AFF;

  font-size: 20px;
  color: 211637;
  border: 0px;
}

.CryptoData{
  width: 20vw;
  margin: auto;
  background-color: #211637;
  padding: 10px;
  border-radius: 20px;
  color: #B18AFF;
}

.greenColored{
  color: #55F595;
}

.redColored{
  color: #A8281D;
}

#day{
  font-size: xx-large;
}

#gold{
  font-size: xx-large;
}

#WorldNews{
  padding-top: 25px;
  padding-bottom: 25px;
  background-color: #B18AFF;
  color: #211637;
  display: flex;
  flex-direction: row;
  justify-content: center;
}


.CryptoTweet{
  padding-top: 25px;
  padding-bottom: 25px;
  background-color: #211637;
  color: #B18AFF;
  display: flex;
  flex-direction: row;
  justify-content: center;
}


#twitterUser{
  font-weight: bold;
  padding-right: 10px;
}

/* STATISTICS */

#StatLottery{
  background-color: #211637;
  width: 100vw;
  display: flex;
  flex-direction: row;
}

.StatLotteryBox{
  margin: auto;
  width: calc(90vw/20);
  background-color: #B18AFF;
}

.StatLotteryBoxName{
  background-color: #211637;
  color: #B18AFF;
}

#StatVariation{
  /*background-color: #211637;*/
  width: 100vw;
  display: flex;
  flex-direction: row;
}

.StatVariationBox{
  margin: auto;
  width: calc(22vw);
  /*background-color: #B18AFF;*/
}

#loseScreen{
  position: absolute;
  top: -100vh;
  left: -100vw;
  width: 100vw;
  height: 100vh;
  display: flex;
  background-color: #211637;
}

#loseScreen h1{
  margin: auto;
  color: #9669F0;
}

.loseDisplay{
  position: absolute;
  top: 0px !important;
  left: 0px !important;
}
